.banner {
    width: 100%;
    
    align-self: center;
}

.descriptionBlock {
    padding: 10px;
}

.homeAvatar {
    width: 200px;
    height: 200px;
}



.inputWrapper {
    width: 100%;
    position: relative;
    margin: 0px ;
    
  }

  

  .inputFile {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }
   
  .inputFileIconWrapper {
    background-color: white;
    opacity: 0.3;
    height: 50px;
    width: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    
  }


  .inputFileIconWrapper:hover { 
    
    opacity: 0.8;
  }
   
  .inputFileButtonText {
    opacity: 0;
    visibility: hidden;
  }
   
  .inputFileButton {
    width: 100%;
    
    height: 0;
    background: none;
    display: flex;
    cursor: pointer;
    
  }